<template>
  <li>
    <div class="goodImg">
      <img
        src="/static/image/banner.png"
        alt=""
      >
    </div>
    <div class="countGroup">
      <h6>背包24-2;背包24-2;背包24-2;背包24-2;背包24-2;背包24-2;</h6>
      <p>包装规格：1件*20个</p>
      <h5>￥320.00</h5>
      <van-icon name="add" size=".53rem" color="#0055ff" @click="onCart" />
      <bulletBox v-show="show" @onClose="onClose" />
    </div>
  </li>
</template>

<script>
import bulletBox from './BulletBox'
export default {
  data () {
    return {
      show: false
    }
  },
  created () {
    
  },
  methods: {
    onCart() {
      this.show = true
    },
    onClose() {
      this.show = false
    }
  },
  components: {
    bulletBox
  }
};
</script>

<style lang="less" scoped>
li {
  position: relative;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  flex-direction: row;
  margin-bottom: .45rem;
  .goodImg {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 1.88rem;
    height: 1.79rem;
    margin-right: .32rem;
    border-radius: 4px;
    background-color: #e9e9e9;
    img {
      max-width: 1.8rem;
      max-height: 2.72rem;
    }
  }
  .countGroup {
    overflow: hidden;
    h6 {
      padding: .1rem 0;
      font-size: 12px;
      line-height: 0.32rem;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    p {
      margin-bottom: .68rem;
      color: #c2c2c2;
      font-size: 9px;
      line-height: 0.24rem;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    h5 {
      color: #000000;
      font-size: 13px;
      line-height: 0.35rem;
      font-weight: 300;
    }
    .van-icon {
      position: absolute;
      right: 0;
      bottom: 0;
    }
  }
}
</style>




