<template>
  <div class="page">
    <!-- 搜索与消息 -->
    <search />
    <div
      class="sortGroup"
      ref="homePage"
    >
      <van-badge-group
        :active-key="activeKey"
        @change="onChange"
      >
        <van-badge title="标签名称" />
        <van-badge title="标签名称" />
        <van-badge title="标签名称" />
        <van-badge title="标签名称" />
        <van-badge title="标签名称" />
        <van-badge title="标签名称" />
        <van-badge title="标签名称" />
        <van-badge title="标签名称" />
        <van-badge title="标签名称" />
        <van-badge title="标签名称" />
        <van-badge title="标签名称" />
        <van-badge title="标签名称" />
      </van-badge-group>
      <div
        class="commodityGroup"
        ref="commodityPage"
      >
        <div class="filterGroup">
          <div class="activeFilter">默认</div>
          <div>销量</div>
          <div>价格</div>
          <div @click="onPopup">筛选
            <img
              src="static/image/filter.png"
              alt=""
            >
          </div>
        </div>
        <ul>
          <good
            v-for="(item,index) in 10"
            :key="index"
          />
        </ul>
      </div>
      <van-popup
        v-model="show"
        position="right"
        :overlay="true"
      >
        <div
          class="popupGroup"
          :style="{height:`${clientHeight - 114}px`}"
        >
          <div>
            <p>价格区间</p>
            <div class="valenceGroup">
              <div class="valence">
                <van-field
                  v-model="lowest"
                  placeholder="最低价"
                  input-align="center"
                />
              </div>
              <span></span>
              <div class="valence">
                <van-field
                  v-model="tallest"
                  placeholder="最高价"
                  input-align="center"
                />
              </div>
            </div>
          </div>
          <div>
            <p>分类</p>
            <div class="valenceGroup">
              <van-checkbox-group v-model="result">
                <van-checkbox
                  v-for="(item, index) in list"
                  :key="index"
                  :name="item"
                >
                  <!-- 复选框 {{ item }} -->
                  <div
                    slot="icon"
                    :class="result[index]==item?'norm':'checkbox'"
                  >{{ item }}</div>
                </van-checkbox>
              </van-checkbox-group>
            </div>
          </div>
          <div class="tool">
            <span class="reset">重置</span>
            <span class="confirm">确认</span>
          </div>
          <div></div>
        </div>
      </van-popup>
    </div>
    <!-- 底部导航栏 -->
    <tabBar :Tabbar="1" />
  </div>
</template>

<script>
import tabBar from "../components/TabBar";
import search from "../components/Search";
import good from "../components/good/SortGood";
export default {
  data() {
    return {
      activeKey: 0,
      show: false,
      clientHeight: "",
      lowest: "",
      tallest: "",
      list: ["a", "b", "c"],
      result: ["a", "c"]
    };
  },
  mounted() {
    // 获取浏览器可视区域高度
    this.clientHeight = `${document.documentElement.clientHeight}`; //document.body.clientWidth;
    //console.log(self.clientHeight);
    window.onresize = function temp() {
      this.clientHeight = `${document.documentElement.clientHeight}`;
    };
  },
  watch: {
    // 如果 `clientHeight` 发生改变，这个函数就会运行
    clientHeight: function() {
      this.changeFixed(this.clientHeight);
    }
  },
  methods: {
    onChange(key) {
      this.activeKey = key;
    },
    onPopup() {
      this.show = true;
    },
    changeFixed(clientHeight) {
      //动态修改样式
      console.log(clientHeight);
      this.$refs.homePage.style.height = clientHeight - 104 + "px";
      // this.$refs.commodityPage.style.height = clientHeight - 104 + "px";
    }
  },
  components: {
    search,
    tabBar,
    good
  }
};
</script>

<style>
.van-badge-group {
  width: 2.67rem;
  overflow-y: auto;
}
.van-hairline--top-bottom::after {
  border: none;
}
.van-badge-group .van-badge {
  border: none;
  padding: 0.45rem 0;
  font-size: 15px;
  text-align: center;
}
.van-badge-group .van-badge::after {
  border: none;
}
.van-overlay {
  position: absolute;
  top: 0;
  left: 0;
}
.van-popup {
  position: absolute;
  top: 49.1%;
  left: 2.63rem;
}
.valenceGroup .van-checkbox-group .van-checkbox .van-checkbox__icon {
  height: 0.8rem;
  line-height: 0.8rem;
}
</style>

<style lang="less" scoped>
.page {
  min-height: 100%;
  background: #f1f1f1;
  .sortGroup {
    position: relative;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    flex-direction: row;
    margin-bottom: 50px;
    overflow-y: auto;
    border-top: solid 1px #f1f1f1;
    .commodityGroup {
      width: 7.33rem;
      background: #ffffff;
      overflow-y: auto;
      .filterGroup {
        display: -webkit-box;
        display: -webkit-flex;
        display: -ms-flexbox;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        height: 0.97rem;
        color: #999999;
        font-size: 15px;
        .activeFilter {
          color: #0055ff;
        }
        img {
          vertical-align: middle;
          width: 0.32rem;
          height: 0.32rem;
        }
      }
      ul {
        padding: 0.2rem 0.24rem;
      }
    }
    .popupGroup {
      position: relative;
      width: 7.37rem;
      background: #ffffff;
      p {
        margin: 0.2rem 0.3rem 0.4rem;
        color: #666666;
        font-size: 15px;
        line-height: 0.4rem;
      }
      .valenceGroup {
        display: -webkit-box;
        display: -webkit-flex;
        display: -ms-flexbox;
        display: flex;
        align-items: center;
        margin: 0 0.3rem 0.6rem;
        .valence {
          .van-cell {
            width: 2rem;
            height: 0.8rem;
            padding: 0;
            border-radius: 15px;
            background: #f1f1f1;
            line-height: 0.8rem;
          }
        }
        span {
          width: 0.44rem;
          height: 1px;
          margin: 0 0.15rem;
          background: #000000;
        }
        .van-checkbox-group {
          display: -webkit-box;
          display: -webkit-flex;
          display: -ms-flexbox;
          display: flex;
          align-items: center;
          .van-checkbox {
            .van-checkbox__icon {
              height: 0.8rem;
              line-height: 0.8rem;
            }
          }
        }
        .checkbox {
          width: 2rem;
          height: 0.8rem;
          padding: 0;
          margin-right: 0.32rem;
          border-radius: 15px;
          background: #f1f1f1;
          color: #000000;
          line-height: 0.8rem;
          text-align: center;
        }
        .norm {
          width: 2rem;
          height: 0.8rem;
          padding: 0;
          margin-right: 0.32rem;
          border-radius: 15px;
          background-color: #0055ff;
          color: #ffffff;
          line-height: 0.8rem;
          text-align: center;
        }
      }
      .tool {
        display: -webkit-box;
        display: -webkit-flex;
        display: -ms-flexbox;
        display: flex;
        flex-direction: row;
        position: absolute;
        left: 50%;
        bottom: .77rem;
        transform: translate(-50%, 0);
        -webkit-transform: translate(-50%, 0);
        color: #ffffff;
        font-size: 18px;
        line-height: 1.07rem;
        text-align: center;
        .reset {
          width: 2.83rem;
          height: 1.07rem;
          background-image: linear-gradient(90deg, #68fae0 0%, #0076fa 100%);
          background-image: -webkit-linear-gradient(90deg, #68fae0 0%, #0076fa 100%);
          border-radius: 19px 0 0 19px;
        }
        .confirm {
          width: 2.83rem;
          height: 1.07rem;
          background-image: linear-gradient(82deg, #0055ff 0%, #013398 100%);
          background-image: -webkit-linear-gradient(82deg, #0055ff 0%, #013398 100%);
          border-radius: 0 19px 19px 0;
        }
      }
    }
  }
}
</style>




